@import url('https://fonts.googleapis.com/css2?family=Goldman&display=swap');

*{
    font-family: 'Roboto';
}

.cont{
    display: flex;
    flex-direction: row;
    gap: 16px;
    padding: 16px;
  }
  
  .prof-cont{
    width: 25%;
    padding: 16px;
  }

  @media only screen and (max-width: 600px) {
    .cont{
      display: flex;
      flex-direction: column;
      gap: 16px;
      padding: 16px;
    }

    .prof-cont{
      justify-content: center;
      align-items: center;
      padding: 16px;
      width: 100%;
    }

    .List{
      display: flex;
      justify-content: center;
      align-items: center;
    }
  }